<template>
  <div id="header">
    <div class="header-content">
      <div class="header-content__avatar">
        <img :src="seller.avatar" alt="">
      </div>
      <div class="header-content__desc">
        <div class="header-content__title">
          <span class="brand bg-image"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="header-content__delivery">{{seller.description}}/{{seller.deliveryTime}}分送达</div>
        <div class="header-content__discount" v-if="seller.supports">
          <span class="icon bg-image"  :class="classMap[this.seller.supports[0].type]"></span>
          <span>{{seller.supports[0].description}}</span>
        </div>
        <div class="header-content__other-discount" v-if="seller.supports">
          <span class="number">{{seller.supports.length}}个</span>
          <span class="icon-chevron-right"></span>
        </div>
      </div>
    </div>
    <div class="header-bulletin">
      <span class="bulletin-icon"></span>
      <span class="bulletin-text">{{seller.bulletin}}</span>
      <span class="icon-chevron-right"></span>
    </div>
    <div class="header-background">
      <img :src="seller.avatar" alt="">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    seller: {
      type: Object
    }
  },
  mounted () {
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
  },
  data () {
    return {
      name: '我是header组件'
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../assets/stylus/index.styl"
  #header
    position relative
    overflow hidden
    .header-content
      display flex
      padding 24px 24px 18px
      margin-bottom 28px
      background rgba(7,17,27,.5)
      .header-content__avatar
        img
          border-radius 2px
          set-img(64px)
      .header-content__desc
        position relative
        flex 1
        display flex
        flex-direction column
        justify-content space-between
        padding-left 16px
        color rgb(255,255,255)
        .header-content__title
          .brand
            display inline-block
            bg-image('brand')
            width 30px
            height 18px
            background-size 30px 18px
            vertical-align top
          .name
            font-size: 16px
            font-weight: bold
        .header-content__delivery
          margin-top 8px
          font-size 12px
          line-height 12px
        .header-content__discount
          position relative
          margin-top 10px
          font-size 12px
          span
            display inline-block
          .icon
            vertical-align top
            width 16px
            height 16px
            background-size 16px 16px
            &.decrease
              bg-image('decrease_1')
            &.discount
              bg-image('discount_1')
            &.guarantee
              bg-image('guarantee_1')
            &.invoice
              bg-image('invoice_1')
            &.special
              bg-image('special_1')
        .header-content__other-discount
          position absolute
          right 20px
          bottom 0
          padding 4px 18px
          font-size 10px
          height 12px
          line-height 12px
          background-color rgba(0,0,0,.2)
          color rgb(255,255,255)
          border-radius 5px
    .header-bulletin
      position absolute
      left 0
      bottom 0
      padding 0 12px
      height 28px
      width 100%;
      line-height 28px
      color rgb(255,255,255)
      font-size 10px
      white-space nowrap
      overflow hidden
      text-overflow ellipsis
      background-color rgba(7,17,27,.2)
      .bulletin-icon
        display inline-block
        width 22px
        height 12px
        bg-image("bulletin")
        background-size 22px 12px
        vertical-align middle
      .bulletin-text
        margin-left 4px
        vertical-align middle
      .icon-chevron-right
        vertical-align middle
    .header-background
      position absolute
      top 0
      left 0
      z-index -1
      width 100%
      filter blur(10px)
      img
        width 100%
</style>



