<template>
  <div id="goods">
    <div class="goodsMenu" ref="goodsMenu">
      <div class="goodsMenu-item" :class="{current : goodsMenuIndex === index}"
           v-for="(item, index) in goods" :key="index" @click="selectItem(index,$event)">
        <div class="goodsMenu-item__wrapper border-1px">
          <span v-show="item.type > 0" class="icon" :class="classMap[item.type]"></span>
          <span class="goodsMenu-item__text">{{item.name}}</span>
        </div>
      </div>
    </div>
    <div class="goodsFoods" ref="goodsFoods">
      <ul class="foodsList">
        <li v-for="(item, index) in goods" :key="index" class="foodsList-item">
          <div class="foodsList-item__title">{{item.name}}</div>
          <ul class="foodsList-item__list">
            <li v-for="(item1, index) in item.foods" :key="index" class="foodsList-item__item1">
              <div class="foodsList-item__img">
                <img :src="item1.icon" alt="">
              </div>
              <div class="foodsList-item__desc border-1px">
                <div class="food-name">{{item1.name}}</div>
                <div v-show="item1.description" class="food-desc">{{item1.description}}</div>
                <div class="food-rating">
                  <span>月售{{item1.sellCount}}份</span>
                  <span>好评率{{item1.rating}}%</span>
                </div>
                <div class="food-price">
                  <span class="food-newprice">￥{{item1.price}}</span>
                  <span v-show="item1.oldPrice" class="food-oldPrice">￥{{item1.oldPrice}}</span>
                </div>
                <div class="cart-control-wrapper">
                  <cart-control :food="item1"></cart-control>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <ShopCart :selectFoods = "selectFoods" :deliverPrice = 'seller.serviceScore' :minPrice="seller.minPrice"></ShopCart>
  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
import BScroll from 'better-scroll'
import ShopCart from './ShopCart'
import CartControl from './CartControl'
export default {
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      goods: [],
      classMap: [],
      heightList: [],
      scrollY: 0,
      goodsMenuIndex: 0
    }
  },
  components: {
    ShopCart,
    CartControl
  },
  methods: {
    selectItem ($index, $event) {
      if (!$event._constructed) {
        return
      }
      this.goodsMenuIndex = $index
      let foodList = this.$refs.goodsFoods.getElementsByClassName('foodsList-item')
      this.foodScroll.scrollToElement(foodList[$index], 300)
    },
    _initscroll () {
      this.menuScroll = new BScroll(this.$refs.goodsMenu, {click: true})
      this.foodScroll = new BScroll(this.$refs.goodsFoods, {probeType: 3, click: true})
      this.foodScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
      this.menuScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    _caculateHeight () {
      let foodList = this.$refs.goodsFoods.getElementsByClassName('foodsList-item__item1')
      let height = 0
      this.heightList.push(height)
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i]
        height += item.clientHeight
        this.heightList.push(height)
      }
    }
  },
  computed: {
    currentIndex () {
      for (let i = 0; i < this.heightList.length; i++) {
        let height1 = this.heightList[i]
        let height2 = this.heightList[i + 1]
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i
        }
      }
      return 0
    },
    selectFoods () {
      let foodsArr = []
      if (this.goods.length !== 0) {
        this.goods.forEach(item => {
          item.foods.forEach(item2 => {
            if (item2.count) {
              foodsArr.push(item2)
            }
          })
        })
      }
      return foodsArr
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
    axios.get('/mock/goods').then((res) => {
      if (res.data.code === 0) {
        this.goods = res.data.data
        Vue.nextTick(() => {
          this._initscroll()
          this._caculateHeight()
        })
      }
    })
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../assets/stylus/index.styl"
  #goods
    display flex
    position absolute
    top 176px
    bottom 48px
    width 100%
    overflow hidden
    .icon
      display inline-block
      width 14px
      height 14px
      vertical-align top
      background-size 14px 14px
      &.decrease
        bg-image('decrease_3')
      &.discount
        bg-image('discount_3')
      &.guarantee
        bg-image('guarantee_3')
      &.invoice
        bg-image('invoice_3')
      &.special
        bg-image('special_3')
    .goodsMenu
      width 90px
      height 100%
      background-color #f3f5f7
      overflow auto
      -webkit-overflow-scrolling touch
      &::-webkit-scrollbar
        width 0
      .goodsMenu-item
        padding 0 12px
        font-size 0
        line-height 16px
        &.current
          position relative
          margin-top -1px
          background-color #fff
          &:after
            border-top 1px solid #fff
          .goodsMenu-item__text
            font-size 12px
            line-height 14px
            font-weight 500
        .goodsMenu-item__wrapper
          display table-cell
          width 80px
          height 64px
          text-align center
          vertical-align middle
          border-1px (rgba(7,17,27,.1))
          .goodsMenu-item__text
            font-size 12px
            line-height 14px
            font-weight 200
    .goodsFoods
      flex 1
      background-color #fff
      .foodsList-item__title
        padding-left 14px
        height 26px
        line-height 26px
        font-size 12px
        color rgb(147,153,159)
        background-color #f3f5f7
        border-left 4px solid #d9dde1
      .foodsList-item__list
        .foodsList-item__item1
          position relative
          display flex
          margin 10px 18px
          padding 18px 0 0 0
          background #fff
          &:nth-child(n+2)
            border-top 1px solid rgba(0,0,0,.4)
          .foodsList-item__img
            width 57px
            height 57px
            img
              width 57px
              height 57px
          .foodsList-item__desc
            flex 1
            padding-left 10px
            .food-name
              margin-top 2px
              font-size 14px
              color rgb(7,17,27)
              line-height 14px
            .food-desc,.food-rating
              margin-top 8px
              font-size 10px
              line-height 16px
              color rgb(147,153,159)
            .food-price
              margin-top 8px
              font-weight 700
              .food-newprice
                font-size 20px
                line-height 24px
                color rgb(240,20,20)
              .food-oldPrice
                vertical-align top
                font-size 10px
                color rgb(147,153,159)
                text-decoration line-through
            .cart-control-wrapper
              position absolute
              right 0
              bottom -5px
</style>



