import Vue from 'vue'
import Router from 'vue-router'
import TheGoods from '../components/TheGoods'
import TheSellers from '../components/TheSellers'
import TheRatings from '../components/TheRatings'
Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/goods'
    },
    {
      path: '/goods',
      name: 'TheGoods',
      component: TheGoods
    },
    {
      path: '/sellers',
      name: 'TheSellers',
      component: TheSellers
    },
    {
      path: '/ratings',
      name: 'TheRatings',
      component: TheRatings
    }
  ]
})



