<template>
  <div id="stars">
    <div class="stars-count" :class="starsCount">
      <div class="starOn">
        <span class="bg-image"></span>
        <span class="bg-image"></span>
        <span class="bg-image"></span>
        <span class="bg-image"></span>
        <span class="bg-image"></span>
      </div>
      <div class="starOff">
        <span class="bg-image"></span>
        <span class="bg-image"></span>
        <span class="bg-image"></span>
        <span class="bg-image"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TheStars',
  props: {
    score: Number
  },
  computed: {
    starsCount () {
      return `stars-count${this.score}`
    }
  }
}

</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../assets/stylus/index.styl"
  #stars
    .stars-count
      .starOn,.starOff
        display inline-block
        height 12px
        overflow hidden
      .starOn
        .bg-image
          display inline-block
          width 12px
          height 12px
          background-size cover
          bg-image('star24_on')
      .starOff
        .bg-image
          display inline-block
          width 12px
          height 12px
          background-size cover
          bg-image('star24_off')
    .stars-count5
      .starOn
        width 80px
      .starOff
        width 0
    .stars-count4
      .starOn
        width 62px
      .starOff
        width 12px
    .stars-count3
      .starOn
        width 45px
      .starOff
        width 29px
    .stars-count2
      .starOn
        width 29px
      .starOff
        width 45px
    .stars-count1
      .starOn
        width 12px
      .starOff
        width 62px
</style>



