<template>
  <div id="sellers">
    <div class="sellers-wrapper">
      <div class="blockItem">
        <div class="blockItem-title">配送信息</div>
        <div class="blockItem-content">
          <span style="background-color:#3366FF;color: #fff;padding: 2px 4px;border-radius: 2px">{{seller.description}}</span>
          <span>约30分钟送达，距离1.6km</span>
          <span>配送费￥{{seller.serviceScore}}</span>
        </div>
      </div>
      <div class="blockItem">
        <div class="blockItem-title">活动与服务</div>
        <div class="blockItem-content">
          <div class="blockItem-content-wrapper">
             <span v-for="(item, index) in seller.supports" :key="index" class="activity-item">
              <span class="icon" :class="classMap[item.type]"></span>
              {{item.description}}
             </span>
          </div>

        </div>
      </div>
      <div class="blockItem">
        <div class="blockItem-title">商家实景</div>
        <div class="blockItem-content">
          <div v-for="(item, index) in seller.pics" :key="index" class="set-img">
            <img :src="item" alt="">
          </div>
        </div>
      </div>
      <div class="blockItem">
        <div class="blockItem-title">商家信息</div>
        <div class="blockItem-content">
          <ul>
            <li v-for="(item, index) in seller.infos" :key="index" class="blockItem-content-info">{{item}}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      name: 'sellers'
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
  }
}

</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../assets/stylus/index.styl"
  .blockItem
    padding 18px
    background-color #fff
    &:nth-child(n+2)
      margin-top 14px
    .blockItem-title
      padding-bottom 14px
      font-size 16px
      font-weight: 700;
      line-height 20px
    .blockItem-content
      font-size 14px
      color #34495e
      font-weight 200
      .blockItem-content-info
        padding 10px 0
      .blockItem-content-wrapper
        display flex
        flex-direction column
        .activity-item
          padding 6px 0
  .icon
    display inline-block
    width 14px
    height 14px
    vertical-align top
    background-size 14px 14px
    &.decrease
      bg-image('decrease_4')
    &.discount
      bg-image('discount_4')
    &.guarantee
      bg-image('guarantee_4')
    &.invoice
      bg-image('invoice_4')
    &.special
      bg-image('special_3')
  #sellers
    background-color: #f2f2f2;
    .sellers-wrapper
      padding 8px
      overflow hidden
    .set-img
      display inline-block
      width 80px
      height 80px
      &:nth-child(n+2)
        margin-left 6px
      img
        width 100%
        height 100%
</style>



