<template>
  <div id="app">
    <TheHeader :seller = 'seller'></TheHeader>
    <div class="tab">
      <div class="tabItem">
        <router-link to="/goods" class="tabItem-a">商品</router-link>
      </div>
      <div class="tabItem">
        <router-link to="/ratings" class="tabItem-a">评论</router-link>
      </div>
      <div class="tabItem">
        <router-link to="/sellers" >商家</router-link>
      </div>
    </div>
    <div class="content">
      <router-view :seller = 'seller'></router-view>
    </div>
  </div>
</template>

<script>
import TheGoods from './components/TheGoods'
import TheHeader from './components/TheHeader'
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      seller: {}
    }
  },
  components: {
    TheGoods,
    TheHeader
  },
  created () {
    axios.get('/mock/seller').then((res) => {
      if (res.data.code === 0) {
        this.seller = res.data.data
      }
    })
  }
}
</script>

<style lang="stylus" ref="stylesheet/stylus">
  @import "assets/stylus/index.styl"
  #app
    .tab
      display flex
      height 40px
      line-height 40px
      border-1px(rgba(240,20,20,.8))
      .tabItem
        flex 1;
        height 40px
        text-align center
        a
          display line-block
          padding 6px 0
          font-size 14px
          line-height 40px
          color rgb(77,85,93)
      .router-link-active
        border-bottom 4px solid #0066CC
</style>



