<template>
  <div id="cartControl">
    <transition-group
    name="fade"
    enter-active-class="animated lightSpeedIn"
    leave-active-class="animated lightSpeedOut"
    :duration="{ enter: 500, leave: 200 }">
      <span class="cartControl-reduce icon-delete" @click="reduceCount"  v-show="food.count > 0" key="reduce111"></span>
      <span class="cartControl-count"  v-show="food.count > 0"  key="number222">{{food.count}}</span>
    </transition-group>

    <span class="cartControl-add icon-add" @click="addCount"></span>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'CartControl',
  props: {
    food: {
      type: Object,
      default () {
        return {
          count: 0
        }
      }
    }
  },
  methods: {
    reduceCount ($event) {
      if (this.food.count === 0) {
        console.log('减法时我要查看food:', this.food)
        return null
      } else {
        this.food.count--
      }
    },
    addCount ($event) {
      if (!this.food.count) {
        Vue.set(this.food, 'count')
        this.food.count = 1
      } else {
        this.food.count++
      }
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../assets/stylus/index.styl"
  #cartControl
    display flex
    font-size 0
    .cartControl-reduce,.cartControl-add
      padding 6px
      display inline-block
      width 24px
      height 24px
      line-height 24px
      font-size 24px
      color #00a0dc
    .cartControl-count
      padding 0 6px
      line-height 36px
      font-size 10px
      text-align center
      color rgb(147,153,159)
      vertical-align top
</style>



