<template>
  <div id="shopcart">
    <div class="shopcart-wrapper">
      <div class="shopcart-icons" @click="showCart">
        <div class="chart-icon icon-shopping_cart" :class="{noZcart : totalCount !== 0}"></div>
        <div class="total-count" v-show="totalCount !== 0">{{totalCount}}</div>
      </div>
      <div class="shopcart-deliver">
        <span class="shopcart-deliver__price border-1px-right" :class="{noZprice: totalPrice !== 0}">￥{{totalPrice}}</span>
        <span class="shopcart-deliver__fee">另需配送费{{deliverPrice}}元</span>
      </div>
      <div class="shopcart-settlement" :class="{settlement: this.totalPrice >= this.minPrice}">{{inform}}</div>
    </div>
    <transition
      enter-active-class="animated rotateInDownLeft"
      leave-active-class="animated rotateOutUpRight">
      <div class="cartList-wrapper" v-show="showCartList">
        <div class="cartList-gray"></div>
        <div class="cartList-content">
          <div class="cartList-content__top">
            <span class="cart-title">购物车</span>
            <span class="cart-clear" @click="clearFoods">清空</span>
          </div>
          <div class="cartList-content__bottom">
            <transition-group
              enter-active-class="animated rotateInDownLeft"
              leave-active-class="animated slideOutRight">
              <div class="cart-item" v-for="(item, index) in selectFoods" :key="index">
                <span class="food-title">{{item.name}}</span>
                <span class="food-price">￥{{item.price}}</span>
                <CartControl class="cart-control" :food="item"></CartControl>
              </div>
            </transition-group>
          </div>
        </div>
      </div>
    </transition>

  </div>
</template>

<script>
import CartControl from './CartControl'
import Vue from 'vue'
export default {
  name: 'ShopCart',
  components: {CartControl},
  data () {
    return {
      fold: false,
      count: 0
    }
  },
  props: {
    deliverPrice: {
      type: Number
    },
    minPrice: {
      type: Number
    },
    selectFoods: {
      type: Array,
      default () {
        return [{
          count: 0
        }]
      }
    }
  },
  computed: {
    totalCount () {
      let totalcount = 0
      this.selectFoods.forEach((item) => {
        totalcount += item.count
      })
      return totalcount
    },
    totalPrice () {
      let totalprice = 0
      this.selectFoods.forEach((item) => {
        totalprice += item.count * item.price
      })
      return totalprice
    },
    inform () {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}`
      } else if (this.totalPrice > 0 && this.totalPrice < this.minPrice) {
        return `还差￥${this.minPrice - this.totalPrice}`
      } else {
        return '去结算'
      }
    },
    showCartList () {
      if (!this.totalCount) {
        this.fold = true
        return false
      }
      return !this.fold
    }
  },
  methods: {
    // 展示购物车的食物列表
    showCart () {
      if (!this.totalCount || this.totalCount !== 0) {
        this.count ++
        if (this.count % 2 !== 0) {
          this.fold = false
        } else {
          this.fold = true
        }
      }
    },
    // 清空购物车的食品列表
    clearFoods () {
      this.selectFoods.forEach(item => {
        Vue.delete(item, 'count')
      })
      this.fold = false
    }
  }
}

</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../assets/stylus/index.styl"
  #shopcart
    position fixed
    bottom 4px
    left 0
    width 100%
    height 48px
    z-index 999
    .shopcart-wrapper
      display flex
      justify-content space-between
      background-color #141d27
      .shopcart-icons
        flex 0 0 80
        position relative
        .chart-icon
          position relative
          margin -10px 0 0 14px
          width 44px
          height 44px
          font-size 24px
          color rgba(255,255,255,.4)
          line-height 44px
          text-align center
          border-radius 44px
          border 6px solid #141d27
          background-color #2b333b
          &.noZcart
            background-color #00a0dc
            color #fff
        .total-count
          position absolute
          right 0
          top -9px
          width  24px
          height 16px
          text-align center
          color #fff
          font-weight 500
          background-color red
          border-radius 16px
          box-shadow 0 4px 8px 0 rgba(0,0,0,.4)
      .shopcart-deliver
        flex 1
        padding 12px
        span
          display inline-block
        .shopcart-deliver__price
          padding-right 14px
          line-height 24px
          font-size 14px
          color rgba(255,255,255,.2)
          border-1px-right(rgba(255,255,255,.4))
          &.noZprice
            color #fff
        .shopcart-deliver__fee
          padding-left 12px
          font-size 12px
          line-height 24px
          font-weight 700
          color rgba(255,255,255,.4)
      .shopcart-settlement
        flex 0 0 105px
        padding 0 8px
        font-size 12px
        text-align center
        line-height 48px
        font-weight 700
        color rgba(255,255,255,.4)
        background-color #2b333b
        &.settlement
          background-color #006600
          color #fff
    .cartList-wrapper
      position fixed
      top 0
      bottom 48px
      width 100%
      display flex
      flex-direction column
      z-index: -1
      .cartList-gray
        flex 1
        background rgba(7,17,27,.6)
      .cartList-content
        width 100%
        position fixed
        bottom 48px
        left 0
        max-height 217px
        background-color #fff
        overflow auto
        .cartList-content__top
          height 40px
          line-height 40px
          padding 0 18px
          border-1px(rgba(7,17,27,.1))
          background-color #f3f5f7
          .cart-title
            font-size 12px
            font-weight 200
            color rgb(7,17,27)
          .cart-clear
            position absolute
            right 18px
            font-size 12px
            color rgb(0,160,220)
        .cartList-content__bottom
          padding 0 18px
          width 100%
          .cart-item
            width 100%
            height 48px
            line-height 48px
            border-1px(rgba(7,17,27,.1))
            .food-title
              font-size 14px
              color rgb(7,17,27)
            .food-price
              position absolute
              right 120px
            .cart-control
              position absolute
              right 18px
              top 6px
</style>



