<template>
  <div id="ratings">
    <div class="reviewTags">
      <span class="reviewTags-tag" :class="{'reviewTags-tag-active': showReview === 6}" @click="showReviews(6)">全部{{ratingsArr.length}}</span>
      <span class="reviewTags-tag" :class="{'reviewTags-tag-active': showReview === 5}" @click="showReviews(5)">非常满意{{ratingsArr5.length}}</span>
      <span class="reviewTags-tag" :class="{'reviewTags-tag-active': showReview === 4}" @click="showReviews(4)">满意{{ratingsArr4.length}}</span>
      <span class="reviewTags-tag" :class="{'reviewTags-tag-active': showReview === 3}" @click="showReviews(3)">还行{{ratingsArr3.length}}</span>
      <span class="reviewTags-tag" :class="{'reviewTags-tag-active': showReview === 2}" @click="showReviews(2)">较不满意{{ratingsArr2.length}}</span>
      <span class="reviewTags-tag" :class="{'reviewTags-tag-active': showReview === 1}" @click="showReviews(1)">不满意{{ratingsArr1.length}}</span>
    </div>
    <div class="reviewList-wrapper">
      <transition-group
        name="rating-fade"
        mode="in-out"
        :duration="{ enter: 1200, leave: 1000 }"
        enter-active-class="animated bounceInUp"
        leave-active-class="animated bounceOutDown">
        <div class="reviewItem" v-if="showReview === 6" v-for="(item, index) in ratingsArr" :key="index">
          <div class="reviewItem-avatar">
            <img :src="item.avatar" alt="">
          </div>
          <div class="reviewItem-content">
            <div class="reviewItem-username">{{item.username}}</div>
            <div class="reviewItem-start">
              <TheStarts :score="item.score"></TheStarts>
            </div>
            <div class="reviewItem-text">{{item.text}}</div>
            <div class="reviewItem-food">
              <span v-for="(item1, index1) in item.recommend" :key="index1" class="reviewItem-foodtype">{{item1,index}}</span>
            </div>
            <div class="reviewItem-reviewTime">{{item.RateTime}}</div>
          </div>
        </div>
        <div class="reviewItem" v-if="showReview === 5" v-for="(item, index) in ratingsArr5" :key="index">
          <div class="reviewItem-avatar">
            <img :src="item.avatar" alt="">
          </div>
          <div class="reviewItem-content">
            <div class="reviewItem-username">{{item.username}}</div>
            <div class="reviewItem-start">
              <TheStarts :score="item.score"></TheStarts>
            </div>
            <div class="reviewItem-text">{{item.text}}</div>
            <div class="reviewItem-food">
              <span v-for="(item1, index1) in item.recommend" :key="index1" class="reviewItem-foodtype">{{item1,index}}</span>
            </div>
            <div class="reviewItem-reviewTime">{{item.RateTime}}</div>
          </div>
        </div>
        <div class="reviewItem" v-if="showReview === 4" v-for="(item, index) in ratingsArr4" :key="index">
          <div class="reviewItem-avatar">
            <img :src="item.avatar" alt="">
          </div>
          <div class="reviewItem-content">
            <div class="reviewItem-username">{{item.username}}</div>
            <div class="reviewItem-start">
              <TheStarts :score="item.score"></TheStarts>
            </div>
            <div class="reviewItem-text">{{item.text}}</div>
            <div class="reviewItem-food">
              <span v-for="(item1, index1) in item.recommend" :key="index1" class="reviewItem-foodtype">{{item1,index}}</span>
            </div>
            <div class="reviewItem-reviewTime">{{item.RateTime}}</div>
          </div>
        </div>
        <div class="reviewItem" v-if="showReview === 3" v-for="(item, index) in ratingsArr3" :key="index">
          <div class="reviewItem-avatar">
            <img :src="item.avatar" alt="">
          </div>
          <div class="reviewItem-content">
            <div class="reviewItem-username">{{item.username}}</div>
            <div class="reviewItem-start">
              <TheStarts :score="item.score"></TheStarts>
            </div>
            <div class="reviewItem-text">{{item.text}}</div>
            <div class="reviewItem-food">
              <span v-for="(item1, index1) in item.recommend" :key="index1" class="reviewItem-foodtype">{{item1,index}}</span>
            </div>
            <div class="reviewItem-reviewTime">{{item.RateTime}}</div>
          </div>
        </div>
        <div class="reviewItem" v-if="showReview === 2" v-for="(item, index) in ratingsArr2" :key="index">
          <div class="reviewItem-avatar">
            <img :src="item.avatar" alt="">
          </div>
          <div class="reviewItem-content">
            <div class="reviewItem-username">{{item.username}}</div>
            <div class="reviewItem-start">
              <TheStarts :score="item.score"></TheStarts>
            </div>
            <div class="reviewItem-text">{{item.text}}</div>
            <div class="reviewItem-food">
              <span v-for="(item1, index1) in item.recommend" :key="index1" class="reviewItem-foodtype">{{item1,index}}</span>
            </div>
            <div class="reviewItem-reviewTime">{{item.RateTime}}</div>
          </div>
        </div>
        <div class="reviewItem" v-if="showReview === 1" v-for="(item, index) in ratingsArr1" :key="index">
          <div class="reviewItem-avatar">
            <img :src="item.avatar" alt="">
          </div>
          <div class="reviewItem-content">
            <div class="reviewItem-username">{{item.username}}</div>
            <div class="reviewItem-start">
              <TheStarts :score="item.score"></TheStarts>
            </div>
            <div class="reviewItem-text">{{item.text}}</div>
            <div class="reviewItem-food">
              <span v-for="(item1, index1) in item.recommend" :key="index1" class="reviewItem-foodtype">{{item1,index}}</span>
            </div>
            <div class="reviewItem-reviewTime">{{item.RateTime}}</div>
          </div>
        </div>
      </transition-group>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
import TheStarts from './TheStars'
export default {
  data () {
    return {
      ratingsArr: [],
      showReview: 6,
      ratingsArr5: [],
      ratingsArr4: [],
      ratingsArr3: [],
      ratingsArr2: [],
      ratingsArr1: []
    }
  },
  components: {
    TheStarts
  },
  methods: {
    showReviews (num) {
      this.showReview = num
    }
  },
  created () {
    axios.get('/mock/ratings').then((res) => {
      this.ratingsData = res.data.data
      let RateTime = ''
      let ratingsObj = {}
      this.ratingsData.forEach(item => {
        RateTime = new Date(item.rateTime).toLocaleDateString()
        ratingsObj = {...item}
        ratingsObj['RateTime'] = RateTime
        this.ratingsArr.push(ratingsObj)
      })

      this.ratingsArr5 = this.ratingsArr.filter(item => {
        return item.score === 5
      })
      this.ratingsArr4 = this.ratingsArr.filter(item => {
        return item.score === 4
      })
      this.ratingsArr3 = this.ratingsArr.filter(item => {
        return item.score === 3
      })
      this.ratingsArr2 = this.ratingsArr.filter(item => {
        return item.score === 2
      })
      this.ratingsArr1 = this.ratingsArr.filter(item => {
        return item.score === 1
      })
    })
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../assets/stylus/index.styl"
  .reviewList-wrapper
    padding 8px 14px
    .reviewItem
      display flex
      padding 6px 0
      &:nth-child(n+2)
       border-top 1px solid rgba(7,17,27,.2)
      .reviewItem-avatar
        width 40px
        height 40px
        img
          width 100%
          height 100%
          border-radius 50%
      .reviewItem-content
        position relative
        flex 1
        display flex
        padding-left 10px
        flex-direction column
        font-size 14px
        .reviewItem-text
          margin 10px 0
          color rgb(77,85,93)
        .reviewItem-food
          margin 4px 0
          .reviewItem-foodtype
            display inline-block
            margin 4px
            padding 4px 6px
            line-height 12px
            font-size 10px
            color rgb(77,85,93)
            background-color #CCCCFF
            border-radius 4px
        .reviewItem-reviewTime
          position absolute
          right 0
          top 0
          font-size 12px
          color rgb(77,85,93)
        .reviewItem-start
          margin-top 4px
  #ratings
    width 100%
    overflow hidden
    .reviewTags
      padding 8px 14px
      border-1px(rgba(7,17,27,.2))
      .reviewTags-tag
        display inline-block
        width 80px
        height 14px
        text-align center
        margin 4px 4px 4px 0
        padding 6px 10px
        color rgb(77,85,93)
        background-color #F1FAFA
        font-size 14px
        border-radius 2px
      .reviewTags-tag-active
        color #fff
        background-color #0099CC
</style>



